

:root {--button-gradient-left: #864FF1;
    --button-gradient-right: #49B3F7;
}

@font-face {
    font-family: 'ALSSchlangesans-Bold';
    src: url(../public/fonts/ALSSchlangesans-Bold.woff);
}

@font-face {
    font-family: 'Gotham Pro';
    src: url(../public/fonts/GothamPro-Medium.woff);
    font-weight: 500;
}

@font-face {
    font-family: 'Gotham Pro';
    src: url(../public/fonts/GothamPro-Bold.woff);
    font-weight: 700;
}

@font-face {
    font-family: 'Gotham Pro';
    src: url(../public/fonts/GothamPro-Light.woff);
    font-weight: 400;
}

$dark-blue: #243976;

.bg-gradient {
    background: linear-gradient(180deg, #FFFFFF 0%, #EEF1FD 100%);
}

.bg-blue {
    background: #EFF2FD;
}

body {
        font-family: 'Gotham Pro';
}

button {
        font-family: 'Gotham Pro';
}

p, h1, h2, h3 {
    color: #243976;
}

h1 {
    margin-bottom: 40px;
    font-family: 'ALSSchlangesans-Bold';
    font-size: 84px;
    line-height: 103.2%;
    color: #243976;
}


h2 {
margin-bottom: 55px;
font-family: 'ALSSchlangesans-Bold';
font-size: 50px;
line-height: 103.2%;
text-align: center;
}


h3 {
font-family: 'ALSSchlangesans-Bold';
font-size: 32px;
line-height: 103.2%;
color: #243976;
}

h4 {
font-weight: 500;
font-size: 20px;
line-height: 103%;
color: #243976;
}



.container {
    margin: 0px 160px;
    max-width: 1350px;
}

@media (max-width: 1100px) {
    .container {
        margin-left: 50px;
        margin-right: 50px;
    }
}

.container-bottom {
    margin-bottom: 80px;
}

.container-inner {
    padding: 0px 160px;
    max-width: 1350px;
}

@media (max-width: 1100px) {
    .container-inner {
        padding: 0px 50px;
    }
}

.purple-button {
    display: block;
    padding: 23px 31px;
    background: linear-gradient(272.77deg, var(--button-gradient-left), var(--button-gradient-right));
    box-shadow: 0px 6px 21px rgba(104, 130, 245, 0.67);
    border-radius: 90px;

    font-weight: 700;
    font-size: 11px;
    line-height: 118.2%;
    letter-spacing: 0.125em;
    text-transform: uppercase;
    color: #FFFFFF;
    transition: all 0.3s ease-in-out;
}

.button-long {
    padding: 27px 44px;
}

.purple-button:hover {
    transition: all 1s ease-in-out;
    --button-gradient-left: #49B3F7;
    --button-gradient-right: #864FF1;
   
}


@media (max-width: 1000px) {

    h1 {
        font-size: 78px;
    }

    h2 {
        font-size: 40px;
    }
}

@media (max-width: 768px) {
    h1 {
        font-size: 60px;
    }

    h2 {
        font-size: 38px;
        margin-bottom: 40px;
    }

    h3 {
    font-size: 26px;
    }

    .container {
        margin-left: 40px;
        margin-right: 40px;
    }
}

@media (max-width: 480px) {
    h1 {
        font-size: 40px;
        margin-bottom: 30px;
    }

    h2 {
        font-size: 34px;
        margin-bottom: 30px;
    }


    h3 {
        font-size: 24px;
        }

        .container {
            margin-left: 20px;
            margin-right: 20px;
        }

        .container-bottom {
            margin-bottom: 70px;
        }
}

@media (max-width: 480px) { 
    .container-bottom {
        margin-bottom: 50px;
    }
}

@media (max-width: 320px) {    h1 {
    font-size: 34px;
}


    h2 {
        font-size: 28px;
    }


    h3 {
        font-size: 20px;
        }

        .container-bottom {
            margin-bottom: 40px;
        }
}